import IntegrationComponent from "./integration-component";

const {
	addAction,
	globalTab
} = JetFBActions;

const {
	WrapperRequiredControl,
} = JetFBComponents;

/**
 * Internal dependencies
 */
const {
	TextControl,
	ToggleControl,
	SelectControl,
	BaseControl,
	Button,
	__experimentalNumberControl,
} = wp.components;

let { NumberControl } = wp.components;

if ( typeof NumberControl === 'undefined' ) {
	NumberControl = __experimentalNumberControl;
}

const { __ } = wp.i18n;

const { withRequestFields } = JetFBHooks;
const { withSelect } = wp.data;

class GetResponseAction extends IntegrationComponent {
	constructor( props ) {
		super( props );

		this.formFieldsList = [ ...this.formFieldsList, ...this.props.requestFields ];
	}

	getFields() {
		const settings = this.props.settings;

		if ( settings.data && settings.data.fields ) {
			return Object.entries( settings.data.fields );
		}
		return [];
	}

	getLists() {
		const settings = this.props.settings;

		if ( settings.data && settings.data.lists ) {
			return this.formatEntriesArray( settings.data.lists );
		}
		return [];
	}

	formatEntriesArray( entries = [], isNeedPlaceholder = true ) {
		const placeholder = {
			label: '--'
		};
		if ( ! entries ) {
			return [];
		}

		const options = Object.entries( entries ).map( ( [ value, label ] ) => {
			return { value, label };
		} );

		return isNeedPlaceholder ? [ placeholder, ...options ] : options;
	}

	render() {
		const { settings, onChange, source, label, help } = this.props;
		const fields = this.getFields();
		const currentTab = globalTab( { slug: 'get-response-tab' } );

		/* eslint-disable jsx-a11y/no-onchange */
		return ( <React.Fragment key="getresponse">
			<ToggleControl
				key={ 'use_global' }
				label={ label( 'use_global' ) }
				checked={ settings.use_global }
				onChange={ use_global => {
					this.onChangeSetting( Boolean( use_global ), 'use_global' )
				} }
			/>
			<BaseControl
				key={ 'getresponse_input_key' }
				className="input-with-button"
			>
				<TextControl
					key='api_key'
					label={ label( 'api_key' ) }
					disabled={ settings.use_global }
					value={ settings.use_global
						? currentTab.api_key
						: settings.api_key
					}
					onChange={ newVal => this.onChangeSetting( newVal, 'api_key' ) }
				/>
				<Button
					key={ 'validate_api_key' }
					isPrimary
					onClick={ () => {
						settings.use_global
							? this.validateAPIKey( currentTab.api_key )
							: this.validateAPIKey()
					} }
					className={ this.state.className.join( ' ' ) + ' jet-form-validate-button' }
				>
					<i className="dashicons"/>
					{ label( 'validate_api_key' ) }
				</Button>
			</BaseControl>
			<div/>
			<div className='margin-bottom--small'>{ help( 'api_key_link_prefix' ) } <a
				href={ help( 'api_key_link' ) }>{ help( 'api_key_link_suffix' ) }</a>
			</div>
			{ settings.isValidAPI && <React.Fragment>
				<BaseControl
					key='getresponse_select_lists'
					className='input-with-button'
				>
					<SelectControl
						key='list_id'
						className="full-width"
						label={ label( 'list_id' ) }
						labelPosition="side"
						value={ settings.list_id }
						onChange={ newVal => this.onChangeSetting( newVal, 'list_id' ) }
						options={ this.getLists() }
					/>
					<Button
						key={ 'update_list_ids' }
						isPrimary
						onClick={ () => {
							settings.use_global
								? this.getApiData( settings.api_key )
								: this.getApiData( currentTab.api_key )
						} }
					>
						{ label( 'update_list_ids' ) }
					</Button>
				</BaseControl>
				<BaseControl
					key={ 'getresponse_day_of_cycle' }
				>
					<NumberControl
						key='day_of_cycle'
						label={ label( 'day_of_cycle' ) }
						labelPosition="side"
						value={ settings.day_of_cycle }
						onChange={ newVal => this.onChangeSetting( Number( newVal ), 'day_of_cycle' ) }
					/>
				</BaseControl>
				<BaseControl
					label={ label( 'fields_map' ) }
					key='getresponse_fields_map'
				>
					<div className='jet-user-fields-map__list'>
						{ fields.map( ( [ fieldName, fieldData ], index ) => {

							return <WrapperRequiredControl
								field={ [ fieldName, fieldData ] }
							>
								<SelectControl
									className="full-width"
									key={ fieldName + index }
									value={ this.getFieldDefault( fieldName ) }
									onChange={ value => this.onChangeFieldMap( value, fieldName ) }
									options={ this.formFieldsList }
								/>
							</WrapperRequiredControl>;
						} ) }
					</div>
				</BaseControl>
			</React.Fragment> }
		</React.Fragment> );
		/* eslint-enable jsx-a11y/no-onchange */
	}
}

addAction( 'getresponse', withSelect( withRequestFields )( GetResponseAction ) )
